@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
margin: 0%;
padding: 0;
box-sizing: border-box;
font-family: 'Mulish', sans-serif;
}

.navbar{
  background-color: #fff !important;
}

.navbar-brand{
  font-size:  1.9rem;
  color: #565387;
}

.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
.navbar a:hover{
  color: #3498db !important;

}

@media (mx-width: 991px){
.navbar-brand{
  font-size: 1.5rem !important;
}
}

.menu :active{
  font-weight: bold;
  border-bottom: none;
}

#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
color: #3498db;
}
#header h2{
  color: #484848;
  font-size: 24px;
}

#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5px;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover{
  background-color: #3498db;
  color: #fff;
}

.header-img{
  text-align: right;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;


}

.img-fluid-animated{

  margin-right: 60px;
}

@-webkit-keyframes up-down{
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
100% {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
}

 @keyframes up-down{
  0%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
100%{
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
} 

.card{

  box-shadow: 2px 5px;
  border: 1px solid #ccc;
}
.card:hover{
  box-shadow: inset 0 0 10px #000000;
}
